import React from 'react';

const Card = ({id, name, email, username}) => {
    return (
        <div className="tc bg-light-green dib br3 pa3 ma2 grow">{/*this is using tachyons for styling*/}
            <img alt='robots' src ={`https://robohash.org/${id}?200x200`}/>
            <div>
                <h2>
                    {name}
                </h2>
               <p>{email}</p>
               <p>{username}</p>
            </div>
        </div>
        
    )
}

export default Card